import Vue from 'vue'

new Vue({
  el: '#app',
  data() {
    return {
        barcodes:''
    }
  },
  render(h) {
      return (
        <div>
            <div class="response">
                <label for="scannedBarcodes">Scanned Barcodes</label>
                <textarea 
                    v-model="barcodes"
                    name="scannedBarcodes" 
                    id="scannedBarcodes" 
                    cols="30" 
                    rows="10"></textarea>
            </div>
            <div class="response">
                {{barcodes}}
            </div>
        </div>
      )
  }
})


